export default function sum(values, valueof) {
  var sum = 0;

  if (valueof === undefined) {
    for (var value of values) {
      if (value = +value) {
        sum += value;
      }
    }
  } else {
    var index = -1;

    for (var _value of values) {
      if (_value = +valueof(_value, ++index, values)) {
        sum += _value;
      }
    }
  }

  return sum;
}