import { isArray, isString } from 'vega-util';
import { getFieldDef, isFieldDef, vgField } from '../../channeldef';
import { duplicate, getFirstDefined, hash } from '../../util';
import { sortParams } from '../common';
import { DataFlowNode } from './dataflow';

function getStackByFields(model) {
  return model.stack.stackBy.reduce((fields, by) => {
    var fieldDef = by.fieldDef;

    var _field = vgField(fieldDef);

    if (_field) {
      fields.push(_field);
    }

    return fields;
  }, []);
}

function isValidAsArray(as) {
  return isArray(as) && as.every(s => isString(s)) && as.length > 1;
}

export class StackNode extends DataFlowNode {
  constructor(parent, stack) {
    super(parent);
    this._stack = stack;
  }

  clone() {
    return new StackNode(null, duplicate(this._stack));
  }

  static makeFromTransform(parent, stackTransform) {
    var {
      stack,
      groupby,
      as,
      offset = 'zero'
    } = stackTransform;
    var sortFields = [];
    var sortOrder = [];

    if (stackTransform.sort !== undefined) {
      for (var sortField of stackTransform.sort) {
        sortFields.push(sortField.field);
        sortOrder.push(getFirstDefined(sortField.order, 'ascending'));
      }
    }

    var sort = {
      field: sortFields,
      order: sortOrder
    };
    var normalizedAs;

    if (isValidAsArray(as)) {
      normalizedAs = as;
    } else if (isString(as)) {
      normalizedAs = [as, "".concat(as, "_end")];
    } else {
      normalizedAs = ["".concat(stackTransform.stack, "_start"), "".concat(stackTransform.stack, "_end")];
    }

    return new StackNode(parent, {
      stackField: stack,
      groupby,
      offset,
      sort,
      facetby: [],
      as: normalizedAs
    });
  }

  static makeFromEncoding(parent, model) {
    var stackProperties = model.stack;
    var {
      encoding
    } = model;

    if (!stackProperties) {
      return null;
    }

    var {
      groupbyChannel,
      fieldChannel,
      offset,
      impute
    } = stackProperties;
    var dimensionFieldDef;

    if (groupbyChannel) {
      var cDef = encoding[groupbyChannel];
      dimensionFieldDef = getFieldDef(cDef); // Fair to cast as groupByChannel is always either x or y
    }

    var stackby = getStackByFields(model);
    var orderDef = model.encoding.order;
    var sort;

    if (isArray(orderDef) || isFieldDef(orderDef)) {
      sort = sortParams(orderDef);
    } else {
      // default = descending by stackFields
      // FIXME is the default here correct for binned fields?
      sort = stackby.reduce((s, field) => {
        s.field.push(field);
        s.order.push(fieldChannel === 'y' ? 'descending' : 'ascending');
        return s;
      }, {
        field: [],
        order: []
      });
    }

    return new StackNode(parent, {
      dimensionFieldDef,
      stackField: model.vgField(fieldChannel),
      facetby: [],
      stackby,
      sort,
      offset,
      impute,
      as: [model.vgField(fieldChannel, {
        suffix: 'start',
        forAs: true
      }), model.vgField(fieldChannel, {
        suffix: 'end',
        forAs: true
      })]
    });
  }

  get stack() {
    return this._stack;
  }

  addDimensions(fields) {
    this._stack.facetby.push(...fields);
  }

  dependentFields() {
    var out = new Set();
    out.add(this._stack.stackField);
    this.getGroupbyFields().forEach(out.add, out);

    this._stack.facetby.forEach(out.add, out);

    this._stack.sort.field.forEach(out.add, out);

    return out;
  }

  producedFields() {
    return new Set(this._stack.as);
  }

  hash() {
    return "Stack ".concat(hash(this._stack));
  }

  getGroupbyFields() {
    var {
      dimensionFieldDef,
      impute,
      groupby
    } = this._stack;

    if (dimensionFieldDef) {
      if (dimensionFieldDef.bin) {
        if (impute) {
          // For binned group by field with impute, we calculate bin_mid
          // as we cannot impute two fields simultaneously
          return [vgField(dimensionFieldDef, {
            binSuffix: 'mid'
          })];
        }

        return [// For binned group by field without impute, we need both bin (start) and bin_end
        vgField(dimensionFieldDef, {}), vgField(dimensionFieldDef, {
          binSuffix: 'end'
        })];
      }

      return [vgField(dimensionFieldDef)];
    }

    return groupby !== null && groupby !== void 0 ? groupby : [];
  }

  assemble() {
    var transform = [];
    var {
      facetby,
      dimensionFieldDef,
      stackField: field,
      stackby,
      sort,
      offset,
      impute,
      as
    } = this._stack; // Impute

    if (impute && dimensionFieldDef) {
      var {
        bandPosition = 0.5,
        bin
      } = dimensionFieldDef;

      if (bin) {
        // As we can only impute one field at a time, we need to calculate
        // mid point for a binned field
        transform.push({
          type: 'formula',
          expr: "".concat(bandPosition, "*") + vgField(dimensionFieldDef, {
            expr: 'datum'
          }) + "+".concat(1 - bandPosition, "*") + vgField(dimensionFieldDef, {
            expr: 'datum',
            binSuffix: 'end'
          }),
          as: vgField(dimensionFieldDef, {
            binSuffix: 'mid',
            forAs: true
          })
        });
      }

      transform.push({
        type: 'impute',
        field,
        groupby: [...stackby, ...facetby],
        key: vgField(dimensionFieldDef, {
          binSuffix: 'mid'
        }),
        method: 'value',
        value: 0
      });
    } // Stack


    transform.push({
      type: 'stack',
      groupby: [...this.getGroupbyFields(), ...facetby],
      field,
      sort,
      as,
      offset
    });
    return transform;
  }

}